import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import { RoutineList } from '../components'
import * as RoutineListActions from '../actions'

const mapStateToProps = (state) => {
  return {
    exerciseList: state.routineList,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(RoutineListActions, dispatch),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(RoutineList)
