import {
  requestExercises,
  receiveExercisesSuccess,
  receiveExercisesError,
} from './index'

export const requestExerciseList = () => async (dispatch) => {
  dispatch(requestExercises())
  await fetch(process.env.PUBLIC_URL + '/exercise_list.json')
    .then((res) => res.json())
    .then((data) => {
      console.log('fetced data', data)
      dispatch(receiveExercisesSuccess(data))
    })
    .catch((e) => {
      console.warn('data fetch error', e)
      dispatch(receiveExercisesError('Could not fetch data'))
    })
}
