import { ActionTypes } from './types'

export function requestExercises() {
  return {
    type: ActionTypes.REQUEST_EXERCISES,
  }
}

export function receiveExercisesSuccess(data) {
  return {
    type: ActionTypes.RECEIVE_EXERCISES_SUCCESS,
    payload: data,
  }
}

export function receiveExercisesError(error) {
  return {
    type: ActionTypes.RECEIVE_EXERCISES_ERROR,
    payload: error,
  }
}

export function startExercise(exercise) {
  return {
    type: ActionTypes.START_EXERCISE,
    payload: exercise
  }
}