import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'

import { Header, Footer } from './common'

function ExerciseList(props) {
  const { exerciseList, actions } = props

  const routineId = props.match.params.id
  const history = useHistory()

  useEffect(() => {
    actions.requestExerciseList(routineId)
  }, [actions, routineId])

  const firstExercise = exerciseList[0]

  const startExercise = () => {
    actions.startExercise(firstExercise)
    history.push(process.env.PUBLIC_URL + '/exercise/1')
  }

  const goBackCallback = () => {
    history.goBack()
  }

  return (
    <div className="exercise-list">
      <Header title="RA routine" onCloseCallback={goBackCallback} />
      <div className="exercise-list__body">
        {exerciseList.map((exercise) => (
          <div key={exercise.id} className="exercise-list__item">
            {exercise.name}
          </div>
        ))}
      </div>
      <Footer>
        <div className="exercise-list__start" onClick={startExercise}>
          Start
        </div>
      </Footer>
    </div>
  )
}

export default ExerciseList
